<template>
    <div class="container mt-5">
        <div class="row">
            <div class="col-md-8 offset-md-2">
                <h1 class="text-center">User Information Form</h1>
                <form @submit.prevent="submitForm">
                    <div class="row mb-3">
                        <div class="col-md-6">
                            <label for="username" class="form-label">Username</label>
                            <input type="text" class="form-control" id="username"
                            @blur="()=>validataName(true)"
                            @input="()=>validataName(false)"
                            v-model="formData.username">
                            <div v-if="errors.username" class="text-danger">
                                {{ errors.username }}
                            </div>
                        </div>

                        <div class="col-md-6">
                            <label for="gender" class="form-label">Gender</label>
                            <select class="form-select" id="gender" 
                            @blur="()=>validateGender(true)"
                            @input="()=>validateGender(false)"
                            v-model="formData.gender">
                                <option value="male">Male</option>
                                <option value="female">Female</option>
                                <option value="other">Other</option>
                            </select>
                            <div v-if="errors.gender" class="text-danger">
                                {{ errors.gender }}
                            </div>
                        </div>
                    </div>

                    <div class="row mb-3">
                        <div class="col-md-6">
                            <label for="password" class="form-label">Password</label>
                            <input type="password" class="form-control" id="password"
                            @blur="()=>validatePassword(true)"
                            @input="()=>validatePassword(false)"
                            v-model="formData.password">
                            <div v-if="errors.password" class="text-danger">
                                {{ errors.password }}
                            </div>
                        </div>

                        <div class="col-md-6">
                            <label for="confirm-password" class="form-label">Confirm password</label>
                            <input type="password" class="form-control" id="confirm-password"
                            v-model="formData.confirmPassword"
                            @blur="() => validateConfirmPassword(true)"/>
                            <div v-if="errors.confirmPassword" class="text-danger">
                                {{ errors.confirmPassword }}
                            </div>
                        </div>
                    </div>

                    <div class="row mb-3">
                        <div class="col-md-12">
                            <div class="form-check">
                                <input type="checkbox" class="form-check-input"
                                id="isAustralian" v-model="formData.isAustralian">
                                <label class="form-check-label" for="isAustralian">Australian Resident?</label>
                            </div>
                        </div>
                    </div>

                    <div class="mb-3">
                        <label for="reason" class="form-label">Reason for joining</label>
                        <textarea class="form-control" id="reason" rows="3" 
                        @blur="()=>validateReason(true)"
                        @input="()=>validateReason(false)"
                        v-model="formData.reason"></textarea>
                        <div v-if="errors.reason" class="text-danger">
                            {{ errors.reason }}
                        </div>
                        <div v-if="verify.reason" class="text-verify">
                            {{ verify.reason }}
                        </div>
                    </div>

                    <div class="mb-3">
                        <label for="suburb" class="form-label">Suburb</label>
                        <input type="text" class="form-control" id="suburb" />
                    </div>

                    <div class="text-center">
                        <button type="submit" class="btn btn-primary me-2" @click="show()">Submit</button>
                        <button type="button" class="btn btn-secondary" @click="clearForm()">Clear</button>
                        <!-- <button type="button" class="btn btn-danger" @click="resetUsersStorage()">Clear All</button> -->
                    </div>

                    <!-- <div class="container mt-5" v-if="submittedCards.length" > 
                        <DataTable :value="submittedCards" tableStyle="min-width: 50rem">
                            <Column 
                                v-for="col in columns" 
                                :key="col.field" 
                                :field="col.field" 
                                :header="col.header">
                                <template #body="{ data }">
                                    <template v-if="col.field === 'isAustralian'">
                                        {{ data[col.field] ? 'Yes' : 'No' }}
                                    </template>
                                    <template v-else>
                                        {{ data[col.field] }}
                                    </template>
                                </template>
                            </Column>
                        </DataTable>
                    </div> -->
                </form>
            </div>
        </div>
    </div>
</template>

<script setup>
    // Our logic will go here
    import { sha256 } from 'js-sha256';
    // import {onErrorCaptured} from 'vue;'
    // import { onMounted } from 'vue';
    import { ref  } from 'vue';
    // import DataTable from 'primevue/datatable';
    // import Column  from 'primevue/column';
    
    const formData = ref({
        username: '',
        password: '',
        confirmPassword: '',
        isAustralian: false,
        reason: '',
        suburb: '',
        gender: ''
    });
    
    const submittedCards = ref([]);

    // const columns = ref([
    //     { field: 'username', header: 'Username' },
    //     { field: 'password', header: 'Password' },
    //     { field: 'isAustralian', header: 'Is Australian' },
    //     { field: 'reason', header: 'Reason' },
    //     { field: 'gender', header: 'Gender' }
    // ]);

    const submitForm = () => {
        validataName(true);
        validatePassword(true);
        validateGender(true);
        validateReason(true);
        if (!errors.value.username && !errors.value.password && !errors.value.gender && !errors.value.reason)
        {   
            const hashedPassword = sha256(formData.value.password)
            submittedCards.value.push({
                ...formData.value,
                password: hashedPassword
            });
            

            let storedUsers = JSON.parse(localStorage.getItem('registeredUsers') || '[]');
            storedUsers.push({
                username: formData.value.username,
                password: hashedPassword,
                isAustralian: formData.value.isAustralian,
                reason: formData.value.reason,
                suburb: formData.value.suburb,
                gender: formData.value.gender
            });
            localStorage.setItem('registeredUsers', JSON.stringify(storedUsers));
            clearForm();
            alert("Registration successful!");
        } 
    };

    const show =()=>{
        console.log(submittedCards.value);
    }

    // const submitForm2 = () => {
    //     const hashedPassword = sha256(formData.value.password)
    //     submittedCards.value.push({
    //         ...formData.value,
    //         password: hashedPassword
    //     });
    // };

    // const resetUsersStorage = () => {
    //     localStorage.setItem('registeredUsers', JSON.stringify([]));
    // };

    const clearForm = () => {
        formData.value = {
            username: '',
            password: '',
            confirmPassword: '',
            isAustralian: false,
            reason: '',
            gender: ''
        };
        errors.value = {
            username: null,
            password: null,
            confirmPassword: null,
            resident: null,
            gender: null,
            reason: null,
        };
        verify.value = {
            username: null,
            password: null,
            confirmPassword: null,
            resident: null,
            gender: null,
            reason: null,
        };
    };

    const errors = ref({
        username: null,
        password: null,
        confirmPassword: null,
        resident: null,
        gender: null,
        reason: null,
    });

    const verify = ref({
        username: null,
        password: null,
        confirmPassword: null,
        resident: null,
        gender: null,
        reason: null,
    });

    // const validataName = (blur)=>{
    //     if (formData.value.username.length<3){
    //         if(blur)
    //         errors.value.username="【error message】\n Name must be at least 3 characters";
    //     }
    //     else{
    //         errors.value.username=null;
    //     }
    // };
    const validataName = (blur) => {
        const username = formData.value.username;
        if (username.length < 3) {
            if (blur) {
                errors.value.username = "【error message】\n Name must be at least 3 characters";
            }
            return; 
        }
        const storedUsers = JSON.parse(localStorage.getItem('registeredUsers'));
        const isDuplicate = storedUsers.some(card => 
            card.username.toLowerCase() === username.toLowerCase()
        );
        if (isDuplicate) {
            if (blur) {
                errors.value.username = "【error message】\n This username is already taken";
            }
        } else {
            errors.value.username = null;
        }
    };

    const validatePassword = (blur)=>{
        const password = formData.value.password;
        const minLength = 8;
        const hasUppercase = /[A-Z]/.test(password);
        const hasLowercase = /[a-z]/.test(password);
        const hasNumber = /[0-9]/.test(password);
        const hasSpecialChar = /[!@#$%^&*(),.?":;{}|<>~`']/.test (password);

        if (password.length<minLength){
            if(blur) errors.value.password=`【error message 1】\n Password must be at least ${minLength} characters long.`;
        }
        else if (!hasUppercase) {
            if(blur) errors.value.password = "【error message 2】\n Password must contain at least one uppercase letter.";
        }
        else if (!hasLowercase){
            if(blur) errors.value.password = "【error message 3】\n Password must contain at least one lowercase letter.";
        }
        else if (!hasSpecialChar){
            if(blur) errors.value.password = "【error message 4】\n Password must contain at least one special character.";
        }
        else if (!hasNumber){
            if(blur) errors.value.password = "【error message 5】\n Password must contain at least one number.";
        }
        else
        {
            errors.value.password=null;
        }
    };

    const validateGender = (blur) => {
        if(formData.value.gender===''){
            if(blur) errors.value.gender = "【error message】\n Gender is required.";
        }
        else{
            errors.value.gender=null;
        }
    };

    const validateReason = (blur) => {
        if(formData.value.reason ===''){
            if(blur) errors.value.reason = "【error message】\n Reason is required.";
        }
        else if(formData.value.reason.toLowerCase().includes('friend')){
            if(blur) verify.value.reason = "【verify message】\n Reason is friend.";
        }
        else{
            errors.value.reason=null;
        }
    };

    const validateConfirmPassword = (blur) => {
        if (formData.value.password !== formData.value.confirmPassword) {
            if (blur) errors.value.confirmPassword = '【error message】Passwords do not match.'
        } else {
            errors.value.confirmPassword = null
        }
    };
</script>

